import React, { useState } from 'react';
import Todolist from './Todolist'

function App() {
    const [item, setItem] = useState('');
    const [items, setItems] = useState([]);
    const handleChange = (event) => {
        let value = event.target.value;
        setItem(value);
    }
    const handleAdd = () => {
        setItems((oldItems) => {
            return [
                ...oldItems, item
            ]
        });
        setItem("");
    }
    const handleDelete = (id) => {
        setItems((oldItems) => {
            return oldItems.filter((arrElement, index) => {
                return index != id;
            })
        })
    }

    return (
        <>
            <div className="main_div" >
                <div className="center_div" >
                    <img src="about.png" alt="image" width="100" style={{ marginTop: 10, borderRadius: 50 }} height="100" />
                    <br />
                    <h1> ASHU'S  TODO-LIST</h1>
                    <br />
                    <input type="text" value={item} onChange={handleChange} placeholder="Add an Item" />
                    <button onClick={handleAdd} >+</button>

                    <ol>
                        {
                            items.map((value, index) => {
                                return <Todolist
                                    text={value}
                                    key={index}
                                    id={index}
                                    onSelect={handleDelete}
                                />
                            })
                        }
                    </ol>
                </div>
            </div>
        </>
    )
}


export default App;